/**
 * AuthContext.tsx
 *
 * Contexto de autenticación para la aplicación.
 * Proporciona funciones de inicio/cierre de sesión y manejo del usuario actual.
 * Persiste los datos en localStorage para mantener la sesión entre recargas.
 */

'use client';
import { createContext, useContext, useEffect, useState } from 'react';

/**
 * Estructura del usuario autenticado
 */
export interface User {
  id: number;
  username: string;
  email: string;
  password?: string;
}

/**
 * Tipado del contexto de autenticación
 */
interface AuthContextType {
  user: User | null;
  login: (user: User) => void;
  logout: () => void;
  isAuthenticated: boolean;
}

// Crear contexto sin valor por defecto
const AuthContext = createContext<AuthContextType | undefined>(undefined);

/**
 * Proveedor de contexto de autenticación.
 * Se encarga de inicializar usuarios por defecto y restaurar sesión desde localStorage.
 */
export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);

  // Al iniciar: carga el usuario logueado
  useEffect(() => {
    const storedUser = localStorage.getItem('kwikemart_user');
    if (storedUser) setUser(JSON.parse(storedUser));
  }, []);

  // Login = setea usuario y guarda en localStorage
  const login = (user: User) => {
    setUser(user);
    localStorage.setItem('user', JSON.stringify(user));
  };

  // Logout = limpia usuario
  const logout = () => {
    setUser(null);
    localStorage.removeItem('user');
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        login,
        logout,
        isAuthenticated: !!user, // true si hay un usuario activo
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

/**
 * Hook personalizado para consumir el contexto de autenticación
 * @throws Error si se usa fuera del <AuthProvider>
 */
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) throw new Error('useAuth debe usarse dentro de <AuthProvider>');
  return context;
};
