// components/navbar/MobileItems.tsx
"use client";

import { useState, type ReactElement } from "react";
import { useRouter } from "next/navigation";

import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";

import {
  Menu,
  House,
  ShoppingCart,
  MessageCircle,
  CirclePercent,
  ShieldCheck,
} from "lucide-react";

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

/** Ítem individual del menú móvil. */
interface LinkItem {
  title: string;
  href: string;
  icon: React.ElementType;
}

/* -------------------------------------------------------------------------- */
/*                             Datos de navegación                            */
/* -------------------------------------------------------------------------- */

const items: LinkItem[] = [
  { title: "Inicio",        href: "/",            icon: House },
  { title: "Tienda",        href: "/productos",   icon: ShoppingCart },
  { title: "Promociones",   href: "/promociones", icon: CirclePercent },
  { title: "Reseñas",       href: "/resenas",     icon: MessageCircle },
  { title: "Seguridad",     href: "/seguridad",   icon: ShieldCheck },
];

/* -------------------------------------------------------------------------- */
/*                              Componente UI                                 */
/* -------------------------------------------------------------------------- */
/**
 * **MobileItems**
 *
 * Menú lateral (_sheet_) para dispositivos móviles con accesos rápidos
 * a las secciones principales.
 *
 * - Controla su estado con `isOpen`.  
 * - Usa `router.push` y cierra el menú con un pequeño retardo para dejar
 *   terminar la animación de cierre.
 */
export default function MobileItems(): ReactElement {
  const [isOpen, setIsOpen] = useState(false);
  const router = useRouter();

  /** Navega y cierra el panel animadamente. */
  const handleNavigate = (href: string) => {
    setIsOpen(false);
    setTimeout(() => router.push(href), 300);
  };

  return (
    <Sheet open={isOpen} onOpenChange={setIsOpen}>
      {/* Botón hamburguesa */}
      <SheetTrigger asChild>
        <button
          className="p-2 rounded-md hover:bg-yellow-500 transition-colors"
          aria-label="Abrir menú móvil"
        >
          <Menu className="h-6 w-6 text-black dark:text-white" />
        </button>
      </SheetTrigger>

      {/* Panel lateral */}
      <SheetContent side="left" className="w-[250px]">
        <SheetHeader>
          <SheetTitle className="text-2xl font-bold">Menú</SheetTitle>
          <SheetDescription className="text-sm text-muted-foreground">
            Bienvenido al Kwik-E-Mart
          </SheetDescription>
        </SheetHeader>

        <nav className="flex flex-col gap-2 mt-6" role="navigation">
          {items.map(({ title, href, icon: Icon }) => (
            <button
              key={title}
              onClick={() => handleNavigate(href)}
              className="flex items-center gap-3 px-4 py-2 rounded-md
                         transition-colors hover:bg-amber-100 hover:text-black
                         focus:outline-none focus:ring-2 focus:ring-amber-300"
            >
              <Icon className="h-5 w-5" aria-hidden="true" />
              <span className="text-sm font-medium">{title}</span>
            </button>
          ))}
        </nav>
      </SheetContent>
    </Sheet>
  );
}
