// components/navbar/Logo.tsx
type LogoProps = {
  className?: string;
};

/**
 * **Logo**
 *
 * Emblema del Kwik-E-Mart inspirado en la serie.
 * Compuesto por un borde azul que abraza el rectángulo verde + texto rojo.
 */
export default function Logo({ className = "" }: LogoProps) {
  return (
    <div className={`inline-flex items-center justify-center ${className}`}>
      <div className="bg-green-500 border-[5px] border-sky-600 rounded-md inline-flex items-center justify-center px-2 py-1">
        <h1 className="bg-white px-4 py-1 text-red-600 text-lg font-extrabold uppercase tracking-wider">
          KWIK-<strong className="text-2xl">E</strong>-MART
        </h1>
      </div>
    </div>
  );
}
