// lib/precio-producto.ts

import { Producto, PromoType } from "./data/productos";

/** Etiqueta promocional amigable para mostrar */
export function getPromoLabel(promo?: PromoType) {
  if (!promo) return null;
  switch (promo.tipo) {
    case "descuento": return `%${promo.valor}`;
    case "2x1": return "2x1";
    case "nuevo": return "¡Nuevo!";
    case "solo-hoy": return "¡Sólo hoy!";
    default: return "";
  }
}

/** Calcula precio unitario con promo. Si no hay promo, retorna el precio original */
export function getPrecioUnitario(producto: Producto): number {
  if (!producto.promo) return producto.precio;
  if (producto.promo.tipo === "descuento") {
    return Math.round((producto.precio * (100 - producto.promo.valor)) / 100 * 100) / 100;
  }
  // Otros tipos no afectan el precio unitario
  return producto.precio;
}

/** Calcula totales finales y etiqueta para mostrar en detalle/carro
 * Si el precio promocional no cambia el valor total, old será null
 */
export function getTotalPromo(promo: any, precio: number, cantidad: number) {
  const precioOriginal = precio * cantidad;

  if (!promo) {
    return {
      total: precioOriginal,
      old: null,
      tag: null,
      value: null,
      unit: precio,
    };
  }

  if (promo.tipo === "descuento") {
    const precioDescuento = Math.round(((precio * (100 - promo.valor)) / 100) * 100) / 100;
    const total = precioDescuento * cantidad;
    // Solo mostrar old si cambia el total
    return {
      total,
      old: total < precioOriginal ? precioOriginal : null,
      tag: "Descuento",
      value: `-${promo.valor}%`,
      unit: precioDescuento,
    };
  }

  if (promo.tipo === "2x1") {
    // Solo hay beneficio si hay más de 1 unidad
    const pagas = Math.ceil(cantidad / 2);
    const total = precio * pagas;
    return {
      total,
      old: total < precioOriginal ? precioOriginal : null,
      tag: "2x1",
      value: "¡Lleva 2, paga 1!",
      unit: precio,
    };
  }

  // Otros tipos (no cambian valor)
  return {
    total: precioOriginal,
    old: null,
    tag: promo.tipo,
    value: null,
    unit: precio,
  };
}
