// components/ThemeProvider.tsx
// -----------------------------------------------------------------------------
// Simple contenedor que expone el `ThemeProvider` de next-themes.
// Se deja como “client” porque maneja state interno de tema y localStorage.
// -----------------------------------------------------------------------------

'use client';

import * as React from 'react';
import { ThemeProvider as NextThemesProvider } from 'next-themes';
import { type ThemeProviderProps } from 'next-themes';

export function ThemeProvider({ children, ...props }: ThemeProviderProps) {
  /*  
   * Pasamos todas las props al provider de Next, p. ej.:
   *   <ThemeProvider attribute="class" defaultTheme="system"> … </ThemeProvider>
   *
   * Esto permite usar `class="dark"` en <html> para Tailwind.
   */
  return <NextThemesProvider {...props}>{children}</NextThemesProvider>;
}
