// components/Navbar.tsx
// -----------------------------------------------------------------------------
//  - Barra superior sticky que cambia de aspecto según la ruta:
//      1. /pago        → versión minimal con botón volver.
//      2. /admin/*     → techo decorativo + acciones admin (sólo desktop).
//      3. (default)    → navegación pública con carrito / wishlist, etc.
//
//  - Incluye integración con Contexts de carrito y auth.
// -----------------------------------------------------------------------------

'use client';

import { useRouter, usePathname } from 'next/navigation';
import { Heart, ShoppingCart, User, LogOut } from 'lucide-react';
import clsx from 'clsx';

import Logo       from './navbar/logo';
import MenuList   from './navbar/menu-list';
import MobileItems from './navbar/menu-mobile';
import ModeToggle from './navbar/toggle-theme';

import { useCartUI } from '@/lib/cart-ui-context';
import { useCart }   from '@/lib/cart-context';
import { useAuth }   from '@/context/AuthContext';

/* --- Constantes de estilo ----------------------------------------------- */
export const NAVBAR_HEIGHT_ID = 'main-navbar';
const ROOF_H      = 160;
const ROOF_COLOR  = '#b46424';
const SKY_SRC     = '/images/sky.jpg';

export default function Navbar() {
  const router     = useRouter();
  const pathname   = usePathname();

  /* -------------------- Carrito ---------------------------------------- */
  const { toggleCart } = useCartUI();
  const { items }      = useCart();
  const totalItems     = items.reduce((sum, i) => sum + i.quantity, 0);

  /* -------------------- Auth ------------------------------------------- */
  const { isAuthenticated, user, logout } = useAuth();

  /* =========================== Modos especiales ======================== */

  /* --- 1. Vista de /pago ---------------------------------------------- */
  if (pathname === '/pago') {
    return (
      <header
        id={NAVBAR_HEIGHT_ID}
        className="fixed inset-x-0 top-0 z-50 bg-background shadow-sm h-26 sm:h-35"
      >
        <div className="mx-auto max-w-6xl h-full px-4 py-4 flex items-center justify-between">
          {/* Botón volver → usa router.back() para conservar estado */}
          <button
            onClick={() => router.back()}
            className="flex items-center text-black font-bold dark:text-white"
          >
            {/* flecha simple SVG inline para evitar dependencias */}
            <svg className="w-6 h-6 mr-2" fill="none" stroke="currentColor" strokeWidth={1.5}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M15 19l-7-7 7-7" />
            </svg>
            Volver
          </button>

          <Logo />

          {/* Placeholder para centrar Logo (ancho igual al botón) */}
          <div className="w-20" />
        </div>
      </header>
    );
  }

  /* --- 2. Vista admin (solo desktop, ≥ sm) ---------------------------- */
  const isAdminRoute = pathname.startsWith('/admin');
  if (isAdminRoute) {
    return (
      <div className="hidden sm:block">
        {/* Techo inclinado decorativo (tres capas + sky corners) */}
        {/* Es puramente visual, pointer-events none para no tapar enlaces */}
        <div
          className="fixed top-0 left-0 z-30 pointer-events-none"
          style={{
            width: '4vw',
            height: ROOF_H,
            backgroundImage: `url(${SKY_SRC})`,
            backgroundSize: 'cover',
            backgroundPosition: 'right top',
            clipPath: 'polygon(0 0,100% 0,0 100%)',
          }}
        />
        <div
          className="fixed top-0 right-0 z-30 pointer-events-none"
          style={{
            width: '4vw',
            height: ROOF_H,
            backgroundImage: `url(${SKY_SRC})`,
            backgroundSize: 'cover',
            backgroundPosition: 'left top',
            clipPath: 'polygon(0 0,100% 0,100% 100%)',
          }}
        />
        {/* Panel central del techo */}
        <div
          className="fixed top-0 left-0 right-0 z-40 pointer-events-none"
          style={{
            height: ROOF_H,
            clipPath: 'polygon(4% 0,96% 0,100% 100%,0 100%)',
            backgroundColor: ROOF_COLOR,
            backgroundImage: `
              linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.25) 100%),
              linear-gradient(130deg, rgba(255,255,255,0.10) 0%, rgba(0,0,0,0.30) 100%),
              repeating-linear-gradient(
                to bottom,
                transparent 0px,
                transparent 32px,
                rgba(0,0,0,0.20) 32px,
                rgba(0,0,0,0.20) 34px
              )
            `,
            boxShadow: '0 6px 10px rgba(0,0,0,0.45)',
          }}
        />
        {/* Rayas verticales del tejado */}
        <div
          className="fixed top-0 left-0 right-0 z-45 pointer-events-none"
          style={{
            height: ROOF_H,
            clipPath: 'polygon(4% 0,96% 0,100% 100%,0 100%)',
            backgroundImage: `
              repeating-linear-gradient(
                to right,
                transparent 0px,
                transparent 67px,
                rgba(0,0,0,0.16) 66px,
                rgba(0,0,0,0.16) 68px
              )
            `,
            transformOrigin: 'bottom center',
            transform: 'perspective(800px) rotateX(15deg)',
          }}
        />
        {/* Logo centrado sobre el techo */}
        <div
          className="fixed top-0 left-0 right-0 z-50 flex items-center justify-center pointer-events-none"
          style={{ height: ROOF_H }}
        >
          <Logo className="scale-200" />
        </div>

        {/* Barra de utilidades admin */}
        <header id={NAVBAR_HEIGHT_ID} className="fixed inset-x-0 top-0 z-50 pointer-events-auto">
          <div className="flex justify-end items-center h-[160px] pr-[6vw] space-x-4">
            {/* Avatar/log out */}
            <div className="bg-white bg-opacity-90 p-2 rounded-full shadow-inner">
              {isAuthenticated ? (
                <LogOut
                  strokeWidth={1}
                  className="text-red-500 hover:text-red-600 cursor-pointer"
                  onClick={() => {
                    logout();
                    router.push('/');
                  }}
                />
              ) : (
                <User
                  strokeWidth={1}
                  className="text-gray-800 cursor-pointer"
                  onClick={() => router.push('/login')}
                />
              )}
            </div>

            {/* Link a home */}
            <button
              onClick={() => router.push('/')}
              className="px-3 py-1 bg-white text-black rounded shadow"
            >
              Volver
            </button>
          </div>
        </header>
      </div>
    );
  }

  /* --- 3. Vista pública por defecto ----------------------------------- */
  return (
    <header
      id={NAVBAR_HEIGHT_ID}
      className={clsx(
        'fixed inset-x-0 top-0 z-50 bg-background shadow-sm',
        'h-26 sm:h-35'
      )}
    >
      <div className="mx-auto max-w-6xl h-full px-4 py-4 flex items-center justify-between">
        {/* Logo clickable → home */}
        <div onClick={() => router.push('/')} className="cursor-pointer flex items-center">
          <Logo />
        </div>

        {/* Navegación (desktop ↔ mobile) */}
        <nav className="hidden sm:flex items-center space-x-6">
          <MenuList />
        </nav>
        <nav className="flex sm:hidden">
          <MobileItems />
        </nav>

        {/* Íconos de acciones (carrito, wishlist, tema, user) */}
        <div className="relative flex items-center gap-3 sm:gap-6">
          {/* Carrito con badge */}
          <div className="relative">
            <ShoppingCart strokeWidth={1} className="cursor-pointer" onClick={toggleCart} />
            {totalItems > 0 && (
              <span className="
                absolute -top-2 -right-2
                bg-green-500 text-black text-xs font-bold
                w-5 h-5 flex items-center justify-center rounded-full
              ">
                {totalItems}
              </span>
            )}
          </div>

          {/* Wishlist (placeholder) */}
          <Heart
            strokeWidth={1}
            className="cursor-pointer"
            onClick={() => router.push('/wishlist')}
          />

          {/* Cambio de tema claro/oscuro */}
          <ModeToggle />

          {/* Perfil / login */}
          {isAuthenticated ? (
            <span className="hidden sm:inline text-sm text-muted-foreground">
              {user?.username}
            </span>
          ) : (
            <User
              strokeWidth={1}
              className="cursor-pointer"
              onClick={() => router.push('/login')}
            />
          )}
        </div>

        {/* Acceso rápido a panel admin solo para id 1 (Apu) */}
        {isAuthenticated && user?.id === 1 && (
          <button
            onClick={() => router.push('/admin/visualizar')}
            className="px-3 py-1 bg-blue-600 text-white rounded shadow"
          >
            admin
          </button>
        )}
      </div>
    </header>
  );
}
