// === Componente que inicializa localStorage al entrar a la app ===
function InitLocalStorage() {
  useEffect(() => {
    if (!localStorage.getItem('kwikemart_productos')) {
      localStorage.setItem('kwikemart_productos', JSON.stringify(productos));
    }
    if (!localStorage.getItem('kwikemart_users')) {
      localStorage.setItem('kwikemart_users', JSON.stringify(users));
    }
    if (!localStorage.getItem('kwikemart_promos')) {
      localStorage.setItem('kwikemart_promos', JSON.stringify(promos));
    }
    if (!localStorage.getItem('kwikemart_promo_productos')) {
      localStorage.setItem('kwikemart_promo_productos', JSON.stringify(promo_productos));
    }
  }, []);
  return null; // No renderiza nada
}

export default InitLocalStorage;
