/* components/ui/sheet.tsx  – versión comentada
 * ---------------------------------------------------------------------------
 * Wrapper minimal  para exponer un componente
 * “Sheet” (panel lateral/top/bottom) con utilidades Tailwind listas.
 *
 */

'use client';

import * as React from 'react';
import * as SheetPrimitive from '@radix-ui/react-dialog';
import { XIcon } from 'lucide-react';
import { cn } from '@/lib/utils';

/* ---------- Root, Trigger y Close (finos wrappers) ---------- */
function Sheet(props: React.ComponentProps<typeof SheetPrimitive.Root>) {
  return <SheetPrimitive.Root data-slot="sheet" {...props} />;
}

function SheetTrigger(props: React.ComponentProps<typeof SheetPrimitive.Trigger>) {
  return <SheetPrimitive.Trigger data-slot="sheet-trigger" {...props} />;
}

function SheetClose(props: React.ComponentProps<typeof SheetPrimitive.Close>) {
  return <SheetPrimitive.Close data-slot="sheet-close" {...props} />;
}

/* ---------- Portal & Overlay ---------- */
function SheetPortal(props: React.ComponentProps<typeof SheetPrimitive.Portal>) {
  return <SheetPrimitive.Portal data-slot="sheet-portal" {...props} />;
}

function SheetOverlay({
  className,
  ...props
}: React.ComponentProps<typeof SheetPrimitive.Overlay>) {
  return (
    <SheetPrimitive.Overlay
      data-slot="sheet-overlay"
      className={cn(
        'fixed inset-0 z-50 bg-black/50 \
         data-[state=open]:animate-in data-[state=closed]:animate-out \
         data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0',
        className
      )}
      {...props}
    />
  );
}

/* ---------- Content (panel) ---------- */
function SheetContent({
  className,
  children,
  side = 'right',
  ...props
}: React.ComponentProps<typeof SheetPrimitive.Content> & {
  side?: 'top' | 'right' | 'bottom' | 'left';
}) {
  return (
    <SheetPortal>
      <SheetOverlay />
      <SheetPrimitive.Content
        data-slot="sheet-content"
        className={cn(
          // Comunes
          'fixed z-50 flex flex-col gap-4 bg-background shadow-lg transition ease-in-out \
           data-[state=open]:animate-in data-[state=closed]:animate-out \
           data-[state=closed]:duration-300 data-[state=open]:duration-500',
          // Lados
          side === 'right' &&
            'inset-y-0 right-0 h-full w-3/4 border-l sm:max-w-sm \
             data-[state=open]:slide-in-from-right data-[state=closed]:slide-out-to-right',
          side === 'left' &&
            'inset-y-0 left-0 h-full w-3/4 border-r sm:max-w-sm \
             data-[state=open]:slide-in-from-left data-[state=closed]:slide-out-to-left',
          side === 'top' &&
            'inset-x-0 top-0 h-auto border-b \
             data-[state=open]:slide-in-from-top data-[state=closed]:slide-out-to-top',
          side === 'bottom' &&
            'inset-x-0 bottom-0 h-auto border-t \
             data-[state=open]:slide-in-from-bottom data-[state=closed]:slide-out-to-bottom',
          className
        )}
        {...props}
      >
        {children}

        {/* Botón de cierre */}
        <SheetPrimitive.Close className="data-[state=open]:bg-secondary ring-offset-background focus:ring-ring absolute right-4 top-4 rounded-xs opacity-70 transition-opacity hover:opacity-100 focus:outline-hidden focus:ring-2 focus:ring-offset-2 disabled:pointer-events-none">
          <XIcon className="size-4" />
          <span className="sr-only">Cerrar</span>
        </SheetPrimitive.Close>
      </SheetPrimitive.Content>
    </SheetPortal>
  );
}

/* ---------- Helpers de layout interno ---------- */
const SheetHeader = (props: React.ComponentProps<'div'>) => (
  <div data-slot="sheet-header" className={cn('flex flex-col gap-1.5 p-4', props.className)} {...props} />
);

const SheetFooter = (props: React.ComponentProps<'div'>) => (
  <div data-slot="sheet-footer" className={cn('mt-auto flex flex-col gap-2 p-4', props.className)} {...props} />
);

const SheetTitle = (
  props: React.ComponentProps<typeof SheetPrimitive.Title>
) => (
  <SheetPrimitive.Title
    data-slot="sheet-title"
    className={cn('font-semibold text-foreground', props.className)}
    {...props}
  />
);

const SheetDescription = (
  props: React.ComponentProps<typeof SheetPrimitive.Description>
) => (
  <SheetPrimitive.Description
    data-slot="sheet-description"
    className={cn('text-sm text-muted-foreground', props.className)}
    {...props}
  />
);

/* ---------- Exports ---------- */
export {
  Sheet,
  SheetTrigger,
  SheetClose,
  SheetContent,
  SheetHeader,
  SheetFooter,
  SheetTitle,
  SheetDescription,
};
