/* ------------------------------------------------------------------
   CONTEXTO GLOBAL DEL CARRITO
------------------------------------------------------------------- */
"use client";

import {
  createContext,
  useCallback,
  useContext,
  useReducer,
  ReactNode,
} from "react";

export type PromoType =
    | { tipo: "descuento"; valor: number } // % de descuento
    | { tipo: "2x1" }
    | { tipo: "nuevo" }
    | { tipo: "solo-hoy" };

/* ▸ CartItem acepta “options” (sabor, tamaño, etc.) */
export interface CartItem {
  id:       string;
  name:     string;
  price:    number;
  quantity: number;
  image?:   string;
  options?: Record<string, string | number>;
  promo?: PromoType;
}

/* ---------------- Reducer ---------------- */
type Action =
  | { type: "add";    payload: CartItem }
  | { type: "remove"; payload: string }                 // id
  | { type: "update"; payload: { id: string; qty: number } }
  | { type: "clear" };

function reducer(state: CartItem[], action: Action): CartItem[] {
  switch (action.type) {
    case "add": {
      const { id, options } = action.payload;
      const idx = state.findIndex(
        (p) =>
          p.id === id &&
          JSON.stringify(p.options ?? {}) === JSON.stringify(options ?? {})
      );

      if (idx >= 0) {
        const copy = [...state];
        copy[idx].quantity += action.payload.quantity;
        return copy;
      }
      return [...state, action.payload];
    }

    case "remove":
      return state.filter((p) => p.id !== action.payload);

    case "update":
      return state.map((p) =>
        p.id === action.payload.id ? { ...p, quantity: action.payload.qty } : p
      );

    case "clear":
      return [];

    default:
      return state;
  }
}

/* --------------- Context --------------- */
interface CartCtx {
  items: CartItem[];
  addItem: (item: CartItem) => void;
  removeItem: (id: string) => void;
  updateItemQuantity: (id: string, qty: number) => void;
  clear: () => void;
}

const CartContext = createContext<CartCtx | null>(null);

export function CartProvider({ children }: { children: ReactNode }) {
  const [items, dispatch] = useReducer(reducer, []);

  const addItem = useCallback(
    (item: CartItem) => dispatch({ type: "add", payload: item }),
    []
  );

  const removeItem = useCallback(
    (id: string) => dispatch({ type: "remove", payload: id }),
    []
  );

  const updateItemQuantity = useCallback(
    (id: string, qty: number) =>
      dispatch({ type: "update", payload: { id, qty } }),
    []
  );

  const clear = useCallback(() => dispatch({ type: "clear" }), []);

  return (
    <CartContext.Provider
      value={{ items, addItem, removeItem, updateItemQuantity, clear }}
    >
      {children}
    </CartContext.Provider>
  );
}

export function useCart() {
  const ctx = useContext(CartContext);
  if (!ctx) throw new Error("useCart() must be inside <CartProvider>");
  return ctx;
}
