/**********************************************************************
 * components/Footer.tsx
 * --------------------------------------------------------------------
 * Pie de página común.  Cambia el estilo cuando la ruta pertenece
 * al dashboard de administración (`/admin/**`):
 *   – Se fija al fondo y muestra efecto de “suelo” del Kwik-E-Mart
 *   – Oculta los enlaces públicos
 *********************************************************************/
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';

const links = [
  { name: 'Inicio',                href: '/'          },
  { name: 'Tienda',                href: '/productos' },
  { name: 'Política de privacidad',href: '/politica'  },
] as const;

const FLOOR_COLOR   = '#b3adb4';
const ADMIN_TXT_CLR = '#333';

export default function Footer() {
  const pathname = usePathname();
  const isAdmin  = pathname.startsWith('/admin');

  return (
    <footer
      id="main-footer"
      className={`
        border-t border-border backdrop-blur-md z-0
        ${isAdmin ? 'fixed bottom-0 left-0 w-full' : 'relative'}
      `}
      style={{
        /* efecto suelo con lamas cada 120 px */
        ...(isAdmin && {
          backgroundColor : FLOOR_COLOR,
          backgroundRepeat: 'repeat-x',
          backgroundSize  : '120px 100%',
          boxShadow       : 'inset 0 5px 12px rgba(0,0,0,.3)',
          transformOrigin : 'top center',
          transform       : 'perspective(300px) rotateX(20deg)',
          backgroundImage : [
            'repeating-linear-gradient(to right, rgba(0,0,0,.1) 0 2px, transparent 2px 120px)',
            'repeating-linear-gradient(to right, transparent 0 2px, rgba(0,0,0,.05) 3px, transparent 4px 120px)',
          ].join(','),
        }),
      }}
    >
      <div className="
          mx-auto flex max-w-screen-xl flex-col items-center gap-4
          px-4 py-6 sm:flex-row sm:justify-between sm:px-10 md:py-8">
        {/* Marca */}
        <p className="text-center text-sm sm:text-left" style={{ color: isAdmin ? ADMIN_TXT_CLR : undefined }}>
          Kwik-<span className="font-semibold">E</span>-Mart
        </p>

        {/* Enlaces públicos (ocultos en admin) */}
        {!isAdmin && (
          <ul className="flex flex-wrap items-center justify-center gap-x-6 gap-y-2 text-sm">
            {links.map(({ name, href }) => (
              <li key={href}>
                <Link href={href} className="text-gray-500 hover:underline dark:text-gray-200">
                  {name}
                </Link>
              </li>
            ))}
          </ul>
        )}
      </div>
    </footer>
  );
}
