/*
 * NavigationMenu Component Set  
 *
 */

import * as React from "react";
import * as NavigationMenuPrimitive from "@radix-ui/react-navigation-menu";
import { cva } from "class-variance-authority";
import { ChevronDownIcon } from "lucide-react";

import { cn } from "@/lib/utils";

/* ─────────────────────────── Root ──────────────────────────── */
// acepta `viewport={false}` para renderizar el
// <NavigationMenuContent> pegado al trigger en vez de usar el viewport global.
function NavigationMenu({
  className,
  children,
  viewport = true,
  ...props
}: React.ComponentProps<typeof NavigationMenuPrimitive.Root> & {
  viewport?: boolean;
}) {
  return (
    <NavigationMenuPrimitive.Root
      data-slot="navigation-menu"
      data-viewport={viewport}
      className={cn(
        "group/navigation-menu relative flex max-w-max flex-1 items-center justify-center",
        className
      )}
      {...props}
    >
      {children}
      {viewport && <NavigationMenuViewport />}
    </NavigationMenuPrimitive.Root>
  );
}

/* ────────────────────────── List & Item ───────────────────────── */
function NavigationMenuList(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.List>
) {
  return (
    <NavigationMenuPrimitive.List
      data-slot="navigation-menu-list"
      className={cn(
        "group flex flex-1 list-none items-center justify-center gap-1",
        props.className
      )}
      {...props}
    />
  );
}

function NavigationMenuItem(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Item>
) {
  return (
    <NavigationMenuPrimitive.Item
      data-slot="navigation-menu-item"
      className={cn("relative", props.className)}
      {...props}
    />
  );
}

/* ───────────────────── Trigger helpers ──────────────────────── */
// `navigationMenuTriggerStyle` -> estilos base reutilizables con cva.
const navigationMenuTriggerStyle = cva(
  "group inline-flex h-9 w-max items-center justify-center rounded-md \
   bg-white px-4 py-2 text-sm font-medium text-black \
   hover:bg-amber-100 hover:text-black \
   focus:bg-amber-200 focus:text-black \
   disabled:pointer-events-none disabled:opacity-50 \
   data-[state=open]:bg-amber-100 data-[state=open]:text-black \
   outline-none transition-[color,box-shadow] \
   focus-visible:ring-amber-100 focus-visible:ring-[3px] focus-visible:outline-1"
);

function NavigationMenuTrigger(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Trigger>
) {
  return (
    <NavigationMenuPrimitive.Trigger
      data-slot="navigation-menu-trigger"
      className={cn(navigationMenuTriggerStyle(), "group", props.className)}
      {...props}
    >
      {props.children}
      <ChevronDownIcon
        className="relative top-[1px] ml-1 size-3 transition duration-300 group-data-[state=open]:rotate-180"
        aria-hidden="true"
      />
    </NavigationMenuPrimitive.Trigger>
  );
}

/* ──────────────────────── Content panel ─────────────────────── */
function NavigationMenuContent(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Content>
) {
  return (
    <NavigationMenuPrimitive.Content
      data-slot="navigation-menu-content"
      className={cn(
        "data-[motion^=from-]:animate-in data-[motion^=to-]:animate-out \
         data-[motion^=from-]:fade-in data-[motion^=to-]:fade-out \
         data-[motion=from-end]:slide-in-from-right-52 data-[motion=from-start]:slide-in-from-left-52 \
         data-[motion=to-end]:slide-out-to-right-52 data-[motion=to-start]:slide-out-to-left-52 \
         top-0 left-0 w-full p-2 pr-2.5 md:absolute md:w-auto",
        // Pop-over look cuando viewport=false
        "group-data-[viewport=false]/navigation-menu:bg-popover \
         group-data-[viewport=false]/navigation-menu:border group-data-[viewport=false]/navigation-menu:shadow \
         group-data-[viewport=false]/navigation-menu:rounded-md group-data-[viewport=false]/navigation-menu:overflow-hidden \
         group-data-[viewport=false]/navigation-menu:top-full group-data-[viewport=false]/navigation-menu:mt-1.5",
        props.className
      )}
      {...props}
    />
  );
}

/* ─────────────────────────── Viewport ────────────────────────── */
function NavigationMenuViewport(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Viewport>
) {
  return (
    <div className="absolute left-0 top-full isolate z-50 flex justify-center">
      <NavigationMenuPrimitive.Viewport
        data-slot="navigation-menu-viewport"
        className={cn(
          "origin-top-center bg-popover text-popover-foreground \
           data-[state=open]:animate-in data-[state=closed]:animate-out \
           data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-90 \
           relative mt-1.5 h-[var(--radix-navigation-menu-viewport-height)] \
           w-full overflow-hidden rounded-md border shadow \
           md:w-[var(--radix-navigation-menu-viewport-width)]",
          props.className
        )}
        {...props}
      />
    </div>
  );
}

/* ─────────────────────────── Link ────────────────────────────── */
function NavigationMenuLink(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Link>
) {
  return (
    <NavigationMenuPrimitive.Link
      data-slot="navigation-menu-link"
      className={cn(
        "flex flex-col gap-1 rounded-sm p-2 text-sm transition-all \
         hover:bg-accent hover:text-accent-foreground focus:bg-accent focus:text-accent-foreground \
         data-[active=true]:bg-accent/50 data-[active=true]:text-accent-foreground \
         focus-visible:ring-ring/50 outline-none focus-visible:ring-[3px] focus-visible:outline-1 \
         [&_svg:not([class*='size-'])]:size-4 [&_svg:not([class*='text-'])]:text-muted-foreground",
        props.className
      )}
      {...props}
    />
  );
}

/* ───────────────────────── Indicator ─────────────────────────── */
function NavigationMenuIndicator(
  props: React.ComponentProps<typeof NavigationMenuPrimitive.Indicator>
) {
  return (
    <NavigationMenuPrimitive.Indicator
      data-slot="navigation-menu-indicator"
      className={cn(
        "top-full z-[1] flex h-1.5 items-end justify-center overflow-hidden \
         data-[state=visible]:animate-in data-[state=hidden]:animate-out \
         data-[state=hidden]:fade-out data-[state=visible]:fade-in",
        props.className
      )}
      {...props}
    >
      <div className="relative top-[60%] h-2 w-2 rotate-45 rounded-tl-sm bg-border shadow-md" />
    </NavigationMenuPrimitive.Indicator>
  );
}

/* ───────────────────────── Exports ───────────────────────────── */
export {
  NavigationMenu,
  NavigationMenuList,
  NavigationMenuItem,
  NavigationMenuContent,
  NavigationMenuTrigger,
  NavigationMenuLink,
  NavigationMenuIndicator,
  NavigationMenuViewport,
  navigationMenuTriggerStyle,
};
